* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

header {
  background-image: url("../images/cover.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed; }

.header__container {
  display: flex;
  flex-direction: column;
  height: 100vh; }

.header__image--background {
  width: 320px;
  height: 568px;
  padding: 0 15px 53px 0; }

.header__image--burger {
  position: fixed;
  justify-content: left;
  background-color: blue;
  width: 44px;
  height: 44px;
  margin: 0 94px 240px 0;
  padding: 15px 14px; }

.header__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.header__tittle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 290px;
  height: 110px;
  padding: 284px 15px 174px 15px;
  font-family: RobotoSlab;
  font-size: 42px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  text-transform: uppercase;
  line-height: 1.31;
  letter-spacing: normal;
  text-align: center;
  color: white; }

.header__paragraph {
  width: 290px;
  height: 21px;
  margin: 10px 0 45px 15px;
  font-family: Roboto;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: white; }

.header__buttondown {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  border-radius: 22.5px;
  background: white; }

footer {
  background-color: palevioletred; }

